import React, { useEffect, useState } from 'react'
import Slider from 'react-slick'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import axios from 'axios';
export default function BrandSlider() {
 let [brands , setBrands] = useState(null)
 async function getBrands(){
    const {data} = await axios.get('https://mcishop.vercel.app/api/v1/brands')
        console.log(data);
        setBrands(data)  
   }

    var settings = {
       dots: false,
       infinite: true,
       autoplaySpeed:2000,
        autoplay:true,
        arrows:false,
       slidesToShow: 6,
       slidesToScroll: 1,
         responsive: [
           {
             breakpoint: 1024,
             settings: {
               slidesToShow: 5,
               slidesToScroll: 5,
               infinite: true,
               dots: true
             }
           },
           {
             breakpoint: 600,
             settings: {
               slidesToShow: 5,
               slidesToScroll: 5,
               initialSlide: 5
             }
           },
           {
             breakpoint: 480,
             settings: {
               // className: "center",
               // centerMode: true,
               // infinite: true,
               //centerPadding: "60px",
               slidesToShow: 1,
               slidesToScroll: 1
             }
           }
         ]
       
       
     }
   
     useEffect(() => {
          getBrands()
     }, [])
  return <>
  <div className="container mt-4">
     <div className=' mx-auto py-4'>
       <h3 className='text-center mb-4 fw-bold'>Our Parteners</h3>
        <Slider {...settings}>
        {brands?.brands.map((brand)=>
          <div key={brand._id} className='mt-3' style={{ margin: "0 20px" }}>
           <img src={brand.logo.path} height={200} alt={brand.name} />
             
             </div>
           )}
       
        </Slider>
       </div>
  </div>
  </>
}
